import styles from './q&a.module.scss'
function QA()
{
    let list = [
        {
            q:'Ti sao xem phim b chm',
            a:'C th do mt s vn  lin quan n h tng mng, gi mng, t cp quang'
        },
        {
            q:'Xem  server ph nh th no',
            a:'Click chut vo mt trong cc server m bn nhn thy di player' 
        },
        {
            q:'V sao ti xem khng c ting',
            a:'Ti server fb ni trng ra l phim c up thng ln facebook, v v l do bn quyn thng l cc bi ht Open, Ending, hoc cc bi nhc m khc cao tro ca phim, chng  b lm tt m thanh  trnh bn quyn'
        },
        {
            q:'Ti phim v my tnh nh th no',
            a:'Dng cc cc hoc nhn nt download pha di video nu c'
        },
    ]
    return(
        <div className={styles.qa}>
            <h1>Mt s kin thc v thc mc.</h1>
            {
                list.map((item,index)=>{
                    return <div className={styles.group} key={index}>
                        <h2>{`${index+1}. ${item.q}.`}</h2>
                        <p>{`+ p : ${item.a}.`}</p>
                    </div>
                })
            }
        </div>
    )
}

export default QA
