function genreToAccent(genre, type) {
    switch (type) {
      case "anime":
        return "Anime";
      case "china":
        return "Hot Hnh Trung Quc";
      case "live-action":
        return "Live Action";
    }
    switch (genre) {
      case "blu-ray":
        return "Blu-ray";
      case "coming-of-age":
        return "Coming Of Age";
      case "martial-art":
        return "Martial Art";
      case "shoujo-ai":
        return "Shoujo Ai";
      case "shounen-ai":
        return "Shounen Ai";
      case "super-power":
        return "Super Power";
      case "doi-thuong":
        return "i Thng";
      case "hoc-duong":
        return "Hc ng";
      case "the-thao":
        return "Th Thao";
      case "trinh-tham":
        return "Trinh Thm";
      case "kinh-di":
        return "Kinh D";
      case "phep-thuat":
        return "Php Thut";
      case "phieu-luu":
        return "Phiu Lu";
      case "hai-huoc":
        return "Hi Hc";
      case "hanh-dong":
        return "Hnh ng";
      case "lich-su":
        return "Lch S";
      case "am-nhac":
        return "m Nhc";
      case "vien-tuong":
        return "Vin Tng";
      case "hoat-hinh-trung-quoc":
        return "Hot Hnh Trung Quc";
      case "hoan-thanh":
        return "Hon Thnh";
      case "dang-tien-hanh":
        return "ang Tin Hnh";
      case "ngay":
        return "Ngy";
      case "tuan":
        return "Tun";
      case "thang":
        return "Thng";
      case "mua-nay":
        return "Ma Ny";
      case "nam-nay":
        return "Nm Ny";
      case "mua-truoc":
        return "Ma Trc";
      case "nam-truoc":
        return "Nm Trc";
      case "tat-ca":
        return "Tt C";
      case "hanh-dong+hai-huoc":
        return "Hnh ng + Hi Hc";
      case "lang-man+hanh-dong":
        return "Lng Mn + Hnh ng";
      case "harem+hai-huoc":
        return "Harem + Hi Hc";
      case "ecchi+harem":
        return "Ecchi + Harem";
      case "doi-thuong+hoc-duong":
        return "i Thng + Hc ng";
      case "hoc-duong+ecchi":
        return "Hc ng + Ecchi";
      case "romance+tragedy":
        return "Romance + Tragedy";
      case "ket-hop-ngau-nhien":
        return "Kt Hp Ngu Nhin";
    }
    genre = [...genre];
    let firstLetter = genre[0].toUpperCase();
    genre.shift();
    return firstLetter + genre.join("");
  }
  
export default genreToAccent 
  